.footer__copyright {
	padding-left: 19px;
	color: #545454;
	font-weight: 400;
	font-size: 18px;
	line-height: 22px;
	margin: 0;
}

@media screen and (max-width: 400px) {
	.footer__copyright {
		font-size: 14px;
		line-height: 17px;
	}
}

@media screen and (max-width: 319px) {
	.footer__copyright {
		padding-left: 17px;
	}
}