.profile__name {
	margin: 0;
	width: 100%;
	max-width: 500px;
	font-weight: 500;
	font-size: 42px;
	line-height: 114%;
	transition: .2s;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	grid-area: name;
}

@media screen and (max-width: 578px) {
	.profile__name {
		font-size: 27px;
		line-height: 33px;
		max-width: 189px;
	}
}