.page {
	font-family: Inter, Arial, sans-serif;
	background-color: #000000;
	padding: 45px 0 60px;
	color: #FFFFFF;
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	min-height: 100vh;
}

@media screen and (max-width: 400px) {
	.page {
		padding: 28px 0 36px;
	}
}